<template>
  <div id="app">
    <div id="nav">
    </div> 
     <router-view/>
  </div>
</template>
<script>
import $ from 'jquery'
import { setCookie } from "./assets/js/cookie"
export default {
  mounted(){
    $(window).bind('scroll', function () {
      if($(this).scrollTop() > 10){
        $('.hero_copyright').removeClass('is-end');
        $('.hero_banner').removeClass('is-end');
        $('.l-header').addClass('is-active');
      } else{
        $('.hero_copyright').addClass('is-end');
        $('.hero_banner').addClass('is-end');
        $('.l-header').removeClass('is-active');
      }
    });
  },
  created() {
    setCookie("view", "", -1);
  }
}
</script>
<style lang="scss">
.cube-grid{
  display: none;
}
body{
  overflow-x: hidden;
  font-family: 'PingFang SC' !important;
}
.c-pagetitle_cnt{
  border-top-right-radius: 15vw;
}
.c-pagetitle_img {
  background: url('./assets/image/kv1.png') center/cover;
}
.businessTitle{
  font-size: 25px;
}
#mini-btn{
  display: none !important;
}
.c-pagetitle_title{
  .en{
    padding-bottom: 0 !important;
    margin-bottom: 0 !important;
    &:after{
      display: none !important;
    }
  }
}
.c-pagetitle{
  &+.c-pagetitle_page{
    .businessIntro{
      position: absolute;
      top: -27vh;
    }
  }
}
@media screen and (min-width: 900px) and (max-width: 1280px) {
  #index .hero_mask{
    top: 49% !important;
  }
  #index .hero_copyright{
    top: 40% !important;
  }
}
</style>
