import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    redirect: '/home'
  },
  {
    path: '/home',
    component: (resolve) => require(["../views/Home.vue"], resolve),
  },
  {
    path: '/zoology',
    name: 'Zoology',
    component: (resolve) => require(["../views/Zoology.vue"], resolve),
  },
  {
    path: '/case',
    name: 'Case',
    component: (resolve) => require(["../views/Case.vue"], resolve),
  },
  {
    path: '/message',
    name: 'Message',
    component: (resolve) => require(["../views/Message.vue"], resolve),
  },
  {
    path: '/relation',
    name: 'Relation',
    component: (resolve) => require(["../views/Relation.vue"], resolve),
  },
  {
    path: '/dream',
    name: 'Dream',
    component: (resolve) => require(["../views/Dream.vue"], resolve),
  },
  // 采薇科技下面的普通级页面
  {
    path: '/explain',
    name: 'Explain',
    component: (resolve) => require(["../views/Home/Explain.vue"], resolve),
  },
  {
    path: '/coreidea',
    name: 'Coreidea',
    component: (resolve) => require(["../views/Home/Coreidea.vue"], resolve),
  },
  {
    path: '/team',
    name: 'Team',
    component: (resolve) => require(["../views/Home/Team.vue"], resolve),
  },
  {
    path: '/honor',
    name: 'Honor',
    component: (resolve) => require(["../views/Home/Honor.vue"], resolve),
  },
  // 联系采薇下面的页面
  {
    path: '/invite',
    name: 'Invite',
    component: (resolve) => require(["../views/Relation/Invite.vue"], resolve),
  }
  // {
  //   path: '/about',
  //   name: 'About',

  //   component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  // }
]

const router = new VueRouter({
  // mode:'history',
  linkActiveClass: 'active',
  routes
})

export default router
